import React, { useState } from 'react';
import {
  Container,
  TextField,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  RadioGroup,
  FormControlLabel,
  Radio,
  Button,
  Typography,
  Snackbar,
  IconButton,
  Grid,
} from '@mui/material';
import { Alert } from '@mui/lab';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';

const RegistrationForm = () => {
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    course: '',
    subField: '',
    year: '',
    rollNumber: '',
    gender: '',
    password: '',
    showPassword: false,
    otp: '',
  });
  const [errors, setErrors] = useState({});
  const [otpSent, setOtpSent] = useState(false);
  const [registrationSuccess, setRegistrationSuccess] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const validateForm = () => {
    const newErrors = {};
    const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const rollNumberPattern = /^[0-9]{8}$/; // Example: 8 digits
    const passwordPattern = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;

    if (!formData.firstName) newErrors.firstName = 'First name is required';
    if (formData.email && !emailPattern.test(formData.email)) newErrors.email = 'Invalid email';
    if (formData.course === 'B.Tech' && !formData.subField) newErrors.subField = 'Sub-field is required for B.Tech';
    if (!formData.year) newErrors.year = 'Current study year is required';
    if (!rollNumberPattern.test(formData.rollNumber)) newErrors.rollNumber = 'Roll number must be 8 digits';
    if (!formData.gender) newErrors.gender = 'Gender is required';
    if (!passwordPattern.test(formData.password)) newErrors.password = 'Password must be at least 8 characters long and include 1 alphabet, 1 numeric, and 1 special character';

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validateForm()) {
      // Simulate sending OTP and show OTP field
      setOtpSent(true);
    }
  };

  const handleOtpSubmit = (e) => {
    e.preventDefault();
    // Here you would validate the OTP
    setRegistrationSuccess(true);
  };

  return (
    <Container maxWidth="sm" sx={{ backgroundColor: '#e3f2fd', padding: '20px', borderRadius: '8px', marginTop: '20px' }}>
      <Typography variant="h5" component="h2" align="center" gutterBottom>
        Registration Form
      </Typography>
      <form onSubmit={handleSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <TextField
              fullWidth
              label="First Name"
              name="firstName"
              required
              value={formData.firstName}
              onChange={handleChange}
              error={!!errors.firstName}
              helperText={errors.firstName}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              fullWidth
              label="Last Name"
              name="lastName"
              value={formData.lastName}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Email"
              name="email"
              required
              value={formData.email}
              onChange={handleChange}
              error={!!errors.email}
              helperText={errors.email}
            />
          </Grid>
          <Grid item xs={12}>
            <FormControl fullWidth required error={!!errors.course}>
              <InputLabel>Course</InputLabel>
              <Select
                name="course"
                value={formData.course}
                onChange={handleChange}
              >
                <MenuItem value="B.Tech">B.Tech</MenuItem>
                <MenuItem value="BCA">BCA</MenuItem>
                <MenuItem value="BBA">BBA</MenuItem>
              </Select>
              {errors.course && <Alert severity="error">{errors.course}</Alert>}
            </FormControl>
          </Grid>
          {formData.course === 'B.Tech' && (
            <Grid item xs={12}>
              <FormControl fullWidth required error={!!errors.subField}>
                <InputLabel>Sub-field</InputLabel>
                <Select
                  name="subField"
                  value={formData.subField}
                  onChange={handleChange}
                >
                  <MenuItem value="CSE">CSE</MenuItem>
                  <MenuItem value="ME">ME</MenuItem>
                  <MenuItem value="EEE">EEE</MenuItem>
                  <MenuItem value="Civil">Civil</MenuItem>
                </Select>
                {errors.subField && <Alert severity="error">{errors.subField}</Alert>}
              </FormControl>
            </Grid>
          )}
          <Grid item xs={12}>
            <FormControl fullWidth required error={!!errors.year}>
              <InputLabel>Current Study Year</InputLabel>
              <Select
                name="year"
                value={formData.year}
                onChange={handleChange}
              >
                <MenuItem value="1st Year">1st Year</MenuItem>
                <MenuItem value="2nd Year">2nd Year</MenuItem>
                <MenuItem value="3rd Year">3rd Year</MenuItem>
              </Select>
              {errors.year && <Alert severity="error">{errors.year}</Alert>}
            </FormControl>
          </Grid>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Roll Number"
              name="rollNumber"
              required
              value={formData.rollNumber}
              onChange={handleChange}
              error={!!errors.rollNumber}
              helperText={errors.rollNumber}
            />
          </Grid>
          <Grid item xs={12}>
            <RadioGroup row name="gender" onChange={handleChange} value={formData.gender}>
              <FormControlLabel value="Male" control={<Radio required />} label="Male" />
              <FormControlLabel value="Female" control={<Radio required />} label="Female" />
              <FormControlLabel value="Other" control={<Radio required />} label="Other" />
            </RadioGroup>
            {errors.gender && <Alert severity="error">{errors.gender}</Alert>}
          </Grid>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Password"
              name="password"
              type={formData.showPassword ? 'text' : 'password'}
              required
              value={formData.password}
              onChange={handleChange}
              error={!!errors.password}
              helperText={errors.password}
              InputProps={{
                endAdornment: (
                  <IconButton
                    onClick={() => setFormData({ ...formData, showPassword: !formData.showPassword })}
                    edge="end"
                  >
                    {formData.showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                ),
              }}
            />
          </Grid>
          <Grid item xs={12}>
            <Button variant="contained" color="primary" type="submit" fullWidth>
              Register
            </Button>
          </Grid>
        </Grid>
      </form>
      {otpSent && (
        <form onSubmit={handleOtpSubmit}>
          <TextField
            fullWidth
            label="Enter OTP"
            name="otp"
            required
            value={formData.otp}
            onChange={handleChange}
            style={{ marginTop: '20px' }}
          />
          <Button variant="contained" color="primary" type="submit" fullWidth style={{ marginTop: '10px' }}>
            Submit OTP
          </Button>
        </form>
      )}
      <Snackbar
        open={registrationSuccess}
        autoHideDuration={6000}
        onClose={() => setRegistrationSuccess(false)}
      >
        <Alert onClose={() => setRegistrationSuccess(false)} severity="success">
          Registration successful!
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default RegistrationForm;
