import React, { useState } from 'react';
import { Box, TextField, Button, Typography, Link, InputAdornment, IconButton } from '@mui/material';
import PersonAddIcon from '@mui/icons-material/PersonAdd';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';

export default function LoginForm() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [isPasswordWrong, setIsPasswordWrong] = useState(false);
  const [isEmailInvalid, setIsEmailInvalid] = useState(false);

  const handleEmailChange = (event) => {
    setEmail(event.target.value);
    setIsEmailInvalid(false); // Reset email invalid flag when typing
  };

  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
    setIsPasswordWrong(false); // Reset wrong password flag when typing
  };

  const handleSubmit = (event) => {
    event.preventDefault();

    // Simple email validation regex
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      setIsEmailInvalid(true);
      return;
    }

    // Mockup login logic (replace with actual authentication logic)
    if (password !== 'correct_password') { // Replace with actual password validation
      setIsPasswordWrong(true);
      return;
    }

    // Handle successful login here
    alert('Login successful!');
  };

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  return (
    <Box
      component="form"
      onSubmit={handleSubmit}
      sx={{
        mt: 3,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start', // Align items to the left
        width: { xs: '90%', sm: '400px' },
        maxWidth: '100%',
        mx: 'auto',
        padding: { xs: 2, sm: 3 },
      }}
    >
      <TextField
        margin="normal"
        required
        fullWidth
        id="email"
        label="Email Address"
        name="email"
        autoComplete="email"
        autoFocus
        value={email}
        onChange={handleEmailChange}
        error={isEmailInvalid}
        helperText={isEmailInvalid ? "Invalid email address." : ""}
      />
      <TextField
        margin="normal"
        required
        fullWidth
        name="password"
        label="Password"
        type={showPassword ? 'text' : 'password'}
        id="password"
        value={password}
        onChange={handlePasswordChange}
        autoComplete="current-password"
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <IconButton
                onClick={handleClickShowPassword}
                onMouseDown={(event) => event.preventDefault()}
                edge="end"
              >
                {showPassword ? <VisibilityOff /> : <Visibility />}
              </IconButton>
            </InputAdornment>
          ),
        }}
      />
      {isPasswordWrong && (
        <Typography color="error" variant="body2" sx={{ mb: 2 }}>
          Incorrect password. Please try again.
        </Typography>
      )}
      <Button
        type="submit"
        fullWidth
        variant="contained"
        sx={{ mt: 3, mb: 2 }}
      >
        Sign In
      </Button>
      <Typography variant="body2" sx={{ width: '100%', textAlign: 'left' }}>
        Don't have an account?{' '}
        <Link href="/signup" sx={{ display: 'flex', alignItems: 'center' }}>
          <PersonAddIcon sx={{ fontSize: 16, mr: 0.5 }} />
          Register here
        </Link>
      </Typography>
      {isPasswordWrong && (
        <Link href="/forgot-password" sx={{ textAlign: 'left', display: 'block', mt: 2 }}>
          Forgot Password?
        </Link>
      )}
    </Box>
  );
}
